<template>
  <ValidationObserver ref="accountReceivableForm" v-slot="{errors,invalid}">
    <section>
      <div class="createAcount-header">
        <div class="createAcount-header_account">
          <div>販売未収金申請</div>
          <div>
            <label class="my-0">申請番号:</label>
            <label class="my-0">決済番号:</label>
          </div>
        </div>
        <div class="createAcount-header_status">
          <div class="createAcount-header_status_item"><p class="mb-0" v-html="status"></p></div>
        </div>
      </div>
      <b-container fluid class="bv-example-row fixScroll">
        <b-row v-if="handleFormError(errors).length > 0" class="errors-messages-container">
          <b-col
                  v-for="(error, index) in handleFormError(errors) "
                  :key="index"
                  sm="12"
          >
            {{ error }}
          </b-col>
        </b-row>
        <section>
          <Collapse>
            <template slot="collapse-header">
              <span class="label_input_header">販促金管理項目</span>
            </template>
            <template slot="collapse-body">
              <b-row class="px-4 py-1">
                <b-col md="4" lg="4" class="pl-0 pr-0">
                  <DateSskf v-model="accountReceivableCreateData.targetOn" valueType="YYYY-MM-DD" id="targetDate"
                            :rules="{required: true}"
                            :messages="{required:'対象年月を確認して下さい'}"
                            name="対象年月"
                            :disabledField="isLockTemplate"
                            formatDate="YYYY/MM"/>
                </b-col>
                <b-col lg="6">
                  <InputSskf id="input-horizontal" name="取引先名" :disabledField="true" :rules="{required: true}"
                             :messages="{required: '取引先名を確認して下さい'}"
                             :maxlength='80'
                             v-model="accountReceivableCreateData.requestModel.torihikiNm"/>
                </b-col>
                <b-col lg="2" class="p-0">
                  <b-button variant="none" class="btn_grey btn_getSupply" @click="showPopupSupply=true">取引先名検索
                  </b-button>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col lg="4" class="pl-0 pr-0">
                  <SelectSskf name="用途" id="use_other_input" :changed="invalid.changed"
                              :rules="{required: true}" v-model="accountReceivableCreateData.purpose"
                              :messages="{required: '用途を確認して下さい'}"
                              :isNormalSelect="true"
                              :options="useTypesList" :disabledField="isLockTemplate">

                  </SelectSskf>
                </b-col>
                <b-col lg="4" class="pl-0" v-if="accountReceivableCreateData.purpose == 'その他'">
                  <InputSskf id="input-horizontal" name=""
                             :messages="{required: '用途その他入力を確認して下さい'}"
                             :maxlength="36" class="fieldDependent" :disabledField="isLockTemplate"
                             v-model="accountReceivableCreateData.purposeOfOthers"/>
                </b-col>
                <b-col lg="4">
                  <SelectSskf name="内口銭/外口銭" id="use_other_input" :changed="invalid.changed"
                              :rules="{required: true}" v-model="accountReceivableCreateData.commissionType"
                              :messages="{required: '内口銭/外口銭を確認して下さい'}"
                              :maxlength="1000"
                              :isNormalSelect="true"
                              :options="commissionTypeList" :disabledField="isLockTemplate"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col md="4" lg="4" class="pl-0 pr-0">
                  <InputNumbericSskf separator="," name="請求金額(円)" id="billing_amount" :disabledField="isLockTemplate"
                                     :messages="{required: '請求金額を確認して下さい'}"
                                     :min=1
                                     :rules="{required: true}"
                                     v-model="accountReceivableCreateData.requestModel.billingAmount"/>
                </b-col>
                <b-col lg="4">
                  <DateSskf v-model="accountReceivableCreateData.requestModel.billingOn" valueType="YYYY-MM-DD"
                            id="billing_date"
                            :changed="invalid.changed"
                            :rules="{required: true}"
                            :messages="{required: '請求日を確認して下さい'}"
                            name="請求日"
                            :disabledField="isLockTemplate"
                            formatDate="YYYY/MM/DD"/>
                </b-col>
              </b-row>

              <b-row class="px-4 py-1 mt-3">
                <b-col lg="4" class="pl-0 pr-0">
                  <SelectSskf @input="getListApplication" name="部門名" id="use_other_input"
                              :rules="{required: true}"
                              :messages="{required: '部門名を確認して下さい'}"
                              v-model="accountReceivableCreateData.requestModel.approvalFlowDetailsRequest.bumonCd"
                              :isNormalSelect="false"
                              optionsLabel="bumonNm"
                              optionsValue="bumonCd"
                              :options="departmentList" :disabledField="isLockTemplate"/>
                </b-col>
                <b-col lg="4">
                  <SelectSskf name="申請者名" id="field2" :changed="invalid.changed"
                              :isNormalSelect="false"
                              optionsLabel="shainNm"
                              optionsValue="shainCd"
                              :rules="{required: true}"
                              :messages="{required: '申請者名を確認して下さい'}"
                              v-model="accountReceivableCreateData.requestModel.approvalFlowDetailsRequest.shainCd"
                              :options="applicationList" :disabledField="isLockTemplate"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col md="12" lg="12" class="pl-0">
                  <TextareaSskf name="備考" id="Remarks" v-model="accountReceivableCreateData.remarks" :row='3'
                                :rules="{required: true}"
                                :messages="{required: '備考を確認して下さい'}"
                                :disabledField="isLockTemplate"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3 attachFile" v-if="isLockTemplate" key="viewListFiles">
                <b-col md="3" lg="auto" class="p-0 attachFile_body border-0">
                  <label class="commonLabel">ファイル添付</label>
                </b-col>
                <b-col md="12" lg="6" class="attachFile_filename">
                  <b-row v-for="(file,index) in attach_file"
                         :key="index"
                         no-gutters
                  >
                    <b-col md="auto" class="px-3">
                      <i class="fas fa-file"></i>
                      <a href="#">{{file.name ? file.name : file.fileOriginalName }}</a>
                    </b-col>
                  </b-row>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 attachFile fixDragFile mt-3" v-else key="uploadFiles">
                <UploadFile
                        :files="attach_file"
                        :rules="{ max_value: 100*1024 }"
                        :messages="{max_value: 'アップロードファイルを確認して下さい'}"
                        @set-attach-file-for-request="setAttachFile"
                        @remove-file="deleteAttachFile"/>
              </b-row>

            </template>
          </Collapse>
        </section>
        <section>
          <Collapse>
            <template slot="collapse-header">
              <span class="label_input_header">支払情報</span>
            </template>
            <template slot="collapse-body">
              <b-row class="px-4 py-1 mt-3">
                <b-col md="4" lg="4" class="pl-0 pr-0">
                  <SelectSskf name="支払場所" id="payment_place" :changed="invalid.changed"
                              :isNormalSelect="false"
                              optionsLabel="bumonNm"
                              optionsValue="bumonNm"
                              :rules="{required: true}" v-model="accountReceivableCreateData.requestModel.paymentPlace"
                              :messages="{required: '支払場所を確認して下さい'}"
                              :options="paymentPlaceList" :disabledField="isLockTemplate"/>
                </b-col>
                <b-col md="4" lg="4" class="pr-0">
                  <SelectSskf name="支払い方法" id="billing_date" :changed="invalid.changed"
                              :isNormalSelect="true"
                              :rules="{required: true}" v-model="accountReceivableCreateData.requestModel.paymentMethod"
                              :messages="{required: '支払い方法を確認して下さい'}"
                              :options="paymentMethodList" :disabledField="isLockTemplate"/>
                </b-col>
                <b-col md="4" lg="4" class="pl-0">
                  <InputSskf v-if="accountReceivableCreateData.requestModel.paymentMethod == 'その他'"
                             id="input-horizontal" name=""
                             :maxlength="200" class="fieldDependent" :disabledField="isLockTemplate"
                             :rules="{required: true}"
                             :messages="{required: 'その他支払い方法を確認して下さい'}"
                             v-model="accountReceivableCreateData.requestModel.paymentOtherMethod"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col md="4" lg="4" class="pl-0 pr-0">
                  <DateSskf v-model="accountReceivableCreateData.requestModel.scheduledPaymentOn" valueType="YYYY-MM-DD"
                            id="payment_scheduled_date"
                            :changed="invalid.changed"
                            :rules="{required: true}"
                            :messages="{required: '支払予定日を確認して下さい'}"
                            name="支払予定日"
                            :disabledField="isLockTemplate"
                            formatDate="YYYY/MM/DD"/>
                </b-col>
                <b-col md="4" lg="4" class="pr-0">
                  <DateSskf v-model="accountReceivableCreateData.requestModel.paymentOn" valueType="YYYY-MM-DD"
                            id="payment_date"
                            :changed="invalid.changed"
                            :rules="{required: true}"
                            :messages="{required: '支払日を確認して下さい'}"
                            name="支払日"
                            :disabledField="isLockTemplate"
                            formatDate="YYYY/MM/DD"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col md="8" lg="8" class="pl-0 pr-0">
                  <InputSskf id="input-payment_destination" name="支払先" :rules="{required: true}" :maxlength="40"
                             :messages="{required: '支払先を確認して下さい'}"
                             v-model="accountReceivableCreateData.requestModel.paymentDestination"/>
                </b-col>
                <b-col md="4" lg="4">
                  <InputSskf id="input-account_number" name="銀行名" :rules="{required: true}" :maxlength="16"
                             :messages="{required: '銀行名を確認して下さい'}"
                             v-model="accountReceivableCreateData.requestModel.bankName"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col md="4" lg="4" class="pl-0 pr-0">
                  <InputSskf id="input-payment_destination" name="口座番号" :rules="{required: true}" :maxlength="10"
                             :messages="{required: '口座番号を確認して下さい'}"
                             v-model="accountReceivableCreateData.requestModel.bankAccountNumber"/>
                </b-col>
                <b-col md="8" lg="8">
                  <InputSskf id="input-account_number" name="口座名義" :rules="{required: true}" :maxlength="40"
                             :messages="{required: '口座名義を確認して下さ'}"
                             v-model="accountReceivableCreateData.requestModel.bankAccountName"/>
                </b-col>
              </b-row>
              <b-row class="px-4 py-1 mt-3">
                <b-col md="10" class="pl-0 ">
                  <table class="table_payment table_payment_1">
                    <tr>
                      <th>支払金額</th>
                      <th>8%対象品目(円)</th>
                      <th>10%対象品目(円)</th>
                      <th>合計金額(円)</th>
                    </tr>
                    <tr>
                      <td>品目</td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="itemTotalForEightPercent"
                                           :disabledField="true"
                                           :rules="{required: true}"
                                           :messages="{required: '8%対象品目計を確認して下さい'}"
                                           classField="fieldPayment"
                                           :value="itemTotalForEightPercentValue"
                        />
                      </td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="itemTotalForTenPercent"
                                           :disabledField="true"
                                           :rules="{required: true}"
                                           :messages="{required: '10%対象品目計を確認して下さい'}"
                                           :value="itemTotalForTenPercentValue"
                                           classField="fieldPayment"
                                           />
                      </td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="itemTotal"  :disabledField="true"
                                           classField="fieldPayment"
                                           :rules="{required:true}"
                                           :messages="{required: '品目合計を確認して下さい'}"
                                           :value="`${itemTotalForEightPercentValue + itemTotalForTenPercentValue}`"/>
                      </td>
                    </tr>
                    <tr>
                      <td>消費税</td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="consumptionTaxTotalForEightPercent"
                                           :disabledField="true"
                                           :rules="{required:true}"
                                           :messages="{required: '8%消費税計を確認して下さい'}"
                                           classField="fieldPayment"
                                           :value="`${itemTotalForEightPercentValue*1.08}`"
                                          />
                      </td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="consumptionTaxTotalForTenPercent"
                                           :disabledField="true"
                                           :rules="{required:true}"
                                           :messages="{required: '10%消費税計を確認して下さい'}"
                                           classField="fieldPayment"
                                           :value="`${itemTotalForTenPercentValue*1.01}`"
                                            />
                      </td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="consumptionTaxTotal"
                                           :disabledField="true"
                                           classField="fieldPayment"
                                           :rules="{required:true}"
                                           :messages="{required: '消費税合計を確認して下さい'}"
                                           :value="`${itemTotalForEightPercentValue*1.08 + itemTotalForTenPercentValue*1.01}`"/>
                      </td>
                    </tr>
                    <tr>
                      <td>合計金額</td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="totalForEightPercent"
                                           :disabledField="true"
                                           :rules="{required:true}"
                                           :messages="{required: '8%対象品目・消費税合計を確認して下さい'}"
                                           classField="fieldPayment"
                                           :value="`${itemTotalForEightPercentValue*2.08}`"/>
                      </td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="totalForTenPercent"  :disabledField="true"
                                           classField="fieldPayment"
                                           :rules="{required:true}"
                                           :messages="{required: '10%対象品目・消費税合計を確認して下さい'}"
                                           :value="`${itemTotalForTenPercentValue*2.01}`"/>
                      </td>
                      <td>
                        <InputNumbericSskf separator="," name="" id="total" :disabledField="true"
                                           classField="fieldPayment"
                                           :rules="{required:true}"
                                           :messages="{required: '総合計を確認して下さい'}"
                                           :value="`${itemTotalForEightPercentValue*2.08 + itemTotalForTenPercentValue*2.01}`"/>
                      </td>
                    </tr>

                  </table>
                </b-col>
              </b-row>
            </template>

          </Collapse>
        </section>
        <!--        table-1-->
        <section>
          <Collapse>
            <template slot="collapse-header">
              <span class="label_input_header">販売未収条件</span>
            </template>
            <template slot="collapse-body">
              <b-row class="px-4 py-1 mt-3 account_receivables_condition">
                <b-col md="10" class="pl-0 account_receivables_condition_table accountTable">
                  <table :style="{minHeight: controlShowSelect.manageItem +'rem'}">
                    <thead>
                    <tr>
                      <th v-for="(item, index) in fieldsPromotionManageItem" :key="index">{{ item }}</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(_item, _index) in listPromotionManageItem" :key="_index" v-show="_item[0].isShow">
                      <td v-for="(item, index) in _item" :key="index" align="center">
                        <validation-provider v-if="item.type == 'text'" v-slot="{invalid,validated}"
                                             :custom-messages="item.messages"
                                             :rules="item.rules">
                          <b-input
                                  :type="item.type"
                                  :maxlength="item.length"
                                  v-model="accountReceivableCreateData.detailsForAccountsReceivablesRequestList[_index][item.name]"
                                  @input="setValueForTableManageItem(_index, 'detailsForAccountsReceivablesRequestList',item.name,$event,index)"
                                  :plaintext="item.plaintext"
                                  :readOnly="item.readOnly || isLockTemplate"
                                  :class="[item.class,{invalidClassTable: invalid && validated}]"
                          ></b-input>
                        </validation-provider>
                        <validation-provider v-if="item.type == 'number'" v-slot="{invalid,validated}" :custom-messages="item.messages"
                                             :rules="item.rules">
                          <v-numeric
                                  v-model="accountReceivableCreateData.detailsForAccountsReceivablesRequestList[_index][item.name]"
                                  @input="setValueForTableManageItem(_index, 'detailsForAccountsReceivablesRequestList',item.name,$event,index)"
                                  :plaintext="item.plaintext"
                                  :read-only="item.readOnly || isLockTemplate"
                                  :class="[item.class,{invalidClassTable: invalid && validated}]"
                                  :min="item.min"
                                  :thousand-separator="item.separator"
                                  :precision="item.precision"
                                  :read-only-class="item.class"
                          ></v-numeric>
                        </validation-provider>
                        <button
                                v-if="item.type == 'button'"
                                :class="item.class"
                                :disabled="item.disabled || isLockTemplate"
                                @click="item['function'](_index, 'detailsForAccountsReceivablesRequestList',_item[0].value)"
                        >
                          <i
                                  v-if="item.icon"
                                  :class="[
                    item.icon,
                    {
                      color_red: item.hascomment == 0,
                      color_blue: item.hascomment == 1,
                      color_grey: item.hascomment == 2,
                    },
                  ]"
                          ></i>
                          {{ item.field }}
                        </button>
                        <validation-provider v-slot="{invalid,validated}" :mode="custom" v-if="item.type == 'select'"
                                             :custom-messages="item.messages" :rules="item.rules">
                          <v-select
                                  v-model="accountReceivableCreateData.detailsForAccountsReceivablesRequestList[_index][item.name]"
                                  @input="setValueForTableManageItem(_index, 'detailsForAccountsReceivablesRequestList',item.name,$event,index)"
                                  label="text"
                                  :reduce="text => text.value"
                                  :options="item.options"
                                  :class="[item.class,{invalidClassTable: invalid && validated}]"
                                  :min="0"
                                  :disabled="item.readOnly || isLockTemplate"
                                  @open="item['onOpen'](_index,index,'listPromotionManageItem',item.name)"
                                  @search=" (value) => {item['onSearch'](_index,index,'listPromotionManageItem',item.name, value)}"
                                  @close="onCloseSelect"
                                  class="main-vselect account-create"
                          ></v-select>
                        </validation-provider>
                      </td>
                    </tr>
                    </tbody>
                  </table>
                </b-col>
                <b-col md="2" class="pl-0 account_receivables_condition_table account_receivables_condition_action">
                  <table>
                    <thead>
                    <tr class="setHeightThTable">
                      <th align="center"
                          v-if="checkEmpty(accountReceivableCreateData.detailsForAccountsReceivablesRequestList)">
                        <button @click="addItemListManageItem({index:1})" :disabled="isLockTemplate"><i
                                class="fas fa-plus-circle"></i></button>
                      </th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(item,index) in listPromotionManageItem" :key="index" v-show="item[0].isShow">
                      <td align="center">
                        <button @click="addItemListManageItem({index:(index+1)})" :disabled="isLockTemplate"><i
                                class="fas fa-plus-circle"></i></button>
                        <button :disabled="isLockTemplate"
                                @click="addItemListManageItem({index:(index+1), isClone: true})"><i
                                class="fas fa-copy"></i></button>
                        <button @click="deleteItemAccountReceivablesDetailRequestList({index: (index+1),listName:'detailsForAccountsReceivablesRequestList'})"
                                :disabled="isLockTemplate">
                          <i class="fas fa-minus-circle" :disabled="isLockTemplate"></i></button>
                      </td>
                    </tr>
                    </tbody>
                  </table>
                </b-col>
              </b-row>
            </template>
          </Collapse>
        </section>
        <!--        table-2-->
        <section>
          <Collapse>
            <template slot="collapse-header"><span class="label_input_header">税8%販促費</span></template>
            <template slot="collapse-body">
              <b-row class="px-4 py-1 mt-3 account_receivables_condition">
                <b-col md="10" class="pl-0 account_receivables_condition_table accountTable">
                  <table :style="{minHeight: controlShowSelect.promotion8 +'rem'}">
                    <thead>
                    <tr>
                      <th v-for="(item, index) in fieldsPromotions" :key="index">{{ item }}</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(_item, _index) in listPromotions8Percent" :key="_index" v-show="_item[0].isShow">
                      <td v-for="(item, index) in _item" :key="index" align="center">
                        <validation-provider v-slot="{invalid,validated}" v-if="item.type == 'text'"
                                             :custom-messages="item.messages"  :rules="item.rules">
                          <b-input
                                  :type="item.type"
                                  :maxlength="item.length"
                                  v-model="accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList[_index][item.name]"
                                  @input="setValueForTablePromotion(_index, 'detailsForPromotionalExpensesRequestTax8PercentList',item.name,$event,index)"
                                  :plaintext="item.plaintext"
                                  :class="[item.class,{invalidClassTable: invalid && validated}]"
                                  :readOnly="item.readOnly || isLockTemplate"
                          ></b-input>
                        </validation-provider>
                        <validation-provider v-slot="{invalid,validated}" v-if="item.type == 'number'"
                                             :custom-messages="item.messages"  :rules="item.rules">
                          <v-numeric
                                  v-model="accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList[_index][item.name]"
                                  @input="setValueForTablePromotion(_index, 'detailsForPromotionalExpensesRequestTax8PercentList',item.name,$event,index)"
                                  :plaintext="item.plaintext"
                                  :readOnly="item.readOnly || isLockTemplate"
                                  :class="[item.class,{invalidClassTable: invalid && validated}]"
                                  :min="item.min"
                                  :thousand-separator="item.separator"
                                  :precision="item.precision"
                                  :read-only-class="item.class"
                          ></v-numeric>
                        </validation-provider>
                        <button
                                v-if="item.type == 'button'"
                                :class="item.class"
                                :disabled="item.disabled || isLockTemplate"
                                @click="item['function'](_index, 'detailsForPromotionalExpensesRequestTax8PercentList', _item[0].value)"
                        >
                          <i
                                  v-if="item.icon"
                                  :class="[
                    item.icon,
                    {
                      color_red: item.hascomment == 0,
                      color_blue: item.hascomment == 1,
                      color_grey: item.hascomment == 2,
                    },
                  ]"
                          ></i>
                          {{ item.field }}
                        </button>
                        <validation-provider v-slot="{invalid,validated}" :mode="custom" v-if="item.type == 'select'"
                                             :custom-messages="item.messages"  :rules="item.rules">
                          <v-select
                                  v-model="accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList[_index][item.name]"
                                  @input="setValueForTablePromotion(_index, 'detailsForPromotionalExpensesRequestTax8PercentList',item.name,$event,index)"
                                  :disabled="item.readOnly || isLockTemplate"
                                  label="text"
                                  :reduce="text => text.value"
                                  :options="item.options"
                                  :class="[item.class,{invalidClassTable: invalid && validated}]"
                                  :min='0'
                                  separator=","
                                  @open="item['onOpen'](_index,index,'listPromotions8Percent',item.name)"
                                  @search="item['onSearch'](_index,index,'listPromotions8Percent',item.name, $event)"
                                  @close="onCloseSelect"
                                  class="main-vselect account-create"
                          ></v-select>
                        </validation-provider>
                      </td>
                    </tr>
