    computed: {
      ...mapState('AccountReceivableCreateModule', ["useTypesList", "accountReceivableCreateData", "currentItemNumber", "listItem",
        "commissionTypeList", "applicationList", "departmentList", "paymentPlaceList", "paymentMethodList", "attach_file", "listPromotionExpenses",
        "listInputType", "listClassification", "listBrandClassification", "listCategory", "listChargeName", "modeAccountReceivable", "showSlide", "isNormalProcess", "tileSlide"
      ]),
      listPromotionManageItem() {
        let list = [];
        if (this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList) {
          for (var i = 0; i < this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList.length; i++) {
            let item = _.cloneDeep(this.itemsPromotionManageItem);
            item[0].value = this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList[i].itemNumber;
            item[0].isShow = this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList[i].isDeleted ? false : true;
            list.push(item);
          }
        }
        return list
      },
      listPromotionsCostLogistics() {
