          for (var i = 0; i < this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList.length; i++) {
            let item = _.cloneDeep(this.itemsPromotions);
            item[0].value = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList[i].itemNumber;
            item[0].isShow = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList[i].isDeleted ? false : true;
            list.push(item);
          }
        }
        return list
      },
      listPromotions10Percent() {
        let list = [];
        if (this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax10PercentList) {
          for (var i = 0; i < this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax10PercentList.length; i++) {
