      },
      isLockTemplate() {
        let isLock = false;
        if (this.modeAccountReceivable == 'confirmCreate') {
          isLock = true;
        }
        return isLock;
      },
      status() {
        if (this.accountReceivableCreateData.requestModel?.isSentBack) {
          return "差し戻"
        } else if (this.modeAccountReceivable == 'create' || this.modeAccountReceivable == 'confirmCreate' || this.modeAccountReceivable == 'reuse') {
          return '作成中'
        } else if (this.modeAccountReceivable == 'edit') {
          return '確認待ち<br>(編集中)'
        } else if (this.accountReceivableCreateData.requestModel.mstRequestStatusesResponse) {
          return this.accountReceivableCreateData.requestModel.mstRequestStatusesResponse.name;
        } else {
          return ""
        }
      },
      // accountReceivableCreateData.requestModel.itemTotalForEightPercent
      itemTotalForEightPercentValue(){
        let value1 = 0;
        let value2 = 0;
        if(!_.isEmpty(this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList)){
          value1 = this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList.reduce((total,item)=>{return total += item.accruedAmount },0)
        }
        if(!_.isEmpty(this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList)){
          value2 = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList.reduce((total,item)=>{return total += item.accruedAmount },0)
        }
        return value1 + value2 ;
      },
      itemTotalForTenPercentValue(){
        let value = 0;
        let value1 = 0;
        if(!_.isEmpty(this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax10PercentList)){
          value = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax10PercentList.reduce((total,item)=>{return total += item.accruedAmount },0)
        }
        if(!_.isEmpty(this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax10PercentList)){
          value1 = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestLogisticFeesList.reduce((total,item)=>{return total += item.accruedAmount },0)
        }
        return value + value1
      },
    },
    watch:{
      itemTotalForEightPercentValue(){
        this.accountReceivableCreateData.requestModel.itemTotalForEightPercent = this.itemTotalForEightPercentValue;
        this.accountReceivableCreateData.requestModel.consumptionTaxTotalForEightPercent = this.itemTotalForEightPercentValue*1.08;
        this.accountReceivableCreateData.requestModel.itemTotal = this.itemTotalForEightPercentValue + this.itemTotalForTenPercentValue;
        this.accountReceivableCreateData.requestModel.consumptionTaxTotal = this.itemTotalForEightPercentValue*1.08 + this.itemTotalForTenPercentValue*1.01;
        this.accountReceivableCreateData.requestModel.totalForEightPercent =  this.itemTotalForEightPercentValue*2.08;
        this.accountReceivableCreateData.requestModel.total = this.itemTotalForEightPercentValue*2.08 + this.itemTotalForTenPercentValue*2.08 ;
      },
      itemTotalForTenPercentValue(){
        this.accountReceivableCreateData.requestModel.itemTotalForTenPercent = this.itemTotalForTenPercentValue;
        this.accountReceivableCreateData.requestModel.consumptionTaxTotalForTenPercent = this.itemTotalForTenPercentValue*1.01
        this.accountReceivableCreateData.requestModel.itemTotal = this.itemTotalForEightPercentValue + this.itemTotalForTenPercentValue;
        this.accountReceivableCreateData.requestModel.consumptionTaxTotal = this.itemTotalForEightPercentValue*1.08 + this.itemTotalForTenPercentValue*1.01;
        this.accountReceivableCreateData.requestModel.totalForTenPercent =  this.itemTotalForTenPercentValue*2.08;
        this.accountReceivableCreateData.requestModel.total = this.itemTotalForEightPercentValue*2.08 + this.itemTotalForTenPercentValue*2.08 ;
      }
    },
    created() {
      // console.log(_.round(4.004, 2))
      this.setModeAccountReceivableCreate('create');
      this.getUseTypes();
      this.getCommissionTypes();
      this.getListDepartment();
      this.getListPaymentPlace();
      this.getListPaymentMethod();
      //go details
      if (this.$route.params.cd) {
        switch (this.$route.params.mode) {
          case 'reuse':
            this.getDetailAccountReceivableReuse({cd: this.$route.params.cd, mode: ''});
            this.setModeAccountReceivableCreate('reuse');
            break;
          case 'edit':
            this.getDetailAccountReceivable({cd: this.$route.params.cd, mode: 'edit'});
            this.setModeAccountReceivableCreate('edit')
        }
      }
    },
    methods: {
      ...mapActions('AccountReceivableCreateModule', ["getUseTypes", "getCommissionTypes", 'getListDepartment', 'getListApplication', 'getListPaymentPlace',
        'getListPaymentMethod', 'createAccountReceivables', "deleteAttachFile", "addItemListManageItem", "addPromotions8Percents",
        "addPromotions10Percents", "addPromotionsCostLogistics", "getListItem", "setAttachFileForRequest",
        "deleteItemAccountReceivablesDetailRequestList", "addItemComment", "addItemAccountReceivablesProduct", "setValueDenpyoNet", 'final_take_unit_price', 'accrued_amount', 'sales_amount', 'final_marginal_profit', 'final_marginal_profit_ratio'
        , "getListPromotionExpenses", "getListInputType", "getListClassifications", "getListBrandClassification", "getListCategory", "getListChargeName", "setChargeNameForRequestTable", "setDepartmentNameForRequestTable"
        , "getDetailAccountReceivable", "setModeAccountReceivableCreate", "resetDataAccountReceivable", "getDetailAccountReceivableReuse", "resetAttachFile", "showSlideNormalProcess"
      ]),
      ...mapMutations('AccountReceivableCreateModule', ['SET_ATTACH_FILE', 'SET_INITIAL_ACCRUE_AMOUNT', 'SET_SUPPLIER_NAME_FOR_TABLE']),
      //handle button controll
      backToEdit() {
        if (this.$route.params) {
          this.setModeAccountReceivableCreate('create')
        } else {
          switch (this.$route.params.mode) {
            case 'edit':
              this.setModeAccountReceivableCreate('edit');
              break;
            case 'reuse':
              this.setModeAccountReceivableCreate('edit');
              break;
          }
        }
      },
     async checkValidateAccountReceivableForm(callback,value){
        let isValid = await this.$refs.accountReceivableForm.validate();
        if(isValid){
          this[callback](value);
        }else{
          let err = await this.$refs.accountReceivableForm.errors;
          console.log(err)
          // if(typeof err == "object") {
          //   this.errors = await Object.values(err).flat();
          // }
        }
      },
      //handle file
      setAttachFile(event) {
        if (!Array.isArray(this.attach_file)) {
          this.resetAttachFile();
        }
        this.setAttachFileForRequest(event)
      },
      getSupplier(event) {
        this.showPopupSupply = false;
        console.log(event);
        let torihikiNm = event.torihiki1Nm + event.torihiki2Nm;
        let torihikiCd = event.torihikiCd;
        this.SET_SUPPLIER_NAME_FOR_TABLE({torihikiNm: torihikiNm, torihikiCd: torihikiCd});
        this.supplier = event;
      },
      //product
      searchProduct(rowIndex, listName, itemNumber) {
        this.selectedItemNumber = {rowIndex: rowIndex, listName: listName, itemNumber: itemNumber};
        this.showPopupSearchProduct = !this.showPopupSearchProduct;
      },
      getProduct(product) {
        this.addItemAccountReceivablesProduct({
          selectedItem: {
            index: this.selectedItemNumber.rowIndex,
            listName: this.selectedItemNumber.listName
          }, product: product
        });
      },
      //comment
      showPopupComment(rowIndex, listName, itemNumber) {
        this.selectedItemNumber = {rowIndex: rowIndex, listName: listName, itemNumber: itemNumber};
        this.isShowPopupComment = true;
        if (this.accountReceivableCreateData[listName][rowIndex].commentDetailsRequests) {
          this.initComment = this.accountReceivableCreateData[listName][rowIndex].commentDetailsRequests[0].comment;
        }
        console.log(this.initComment)
        if (this.modeAccountReceivable == 'create') {
          this.commentRecordCd = this.accountReceivableCreateData[listName][rowIndex]?.accountReceivableDetailCd;
        }
        //show loading
      },
      onAddedComment(newComment) {
        this.addItemComment({
          selectedItem: {
            index: this.selectedItemNumber.rowIndex,
            listName: this.selectedItemNumber.listName
          }, comment: newComment
        });
      },
      onOpenSelectDepartment(rowIndex, columnIndex, listName, fieldName) {
        console.log(rowIndex, columnIndex, listName, fieldName)
        let shainCd =  '';
        switch (listName) {
          case "listPromotionManageItem":
            shainCd = this.accountReceivableCreateData.detailsForAccountsReceivablesRequestList[rowIndex].shainCd;
            break;
          case "listPromotions8Percent":
            shainCd = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax8PercentList[rowIndex].shainCd;
            break;
          case "listPromotions10Percent":
            shainCd = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestTax10PercentList[rowIndex].shainCd;
            break;
          case "listPromotionsCostLogistics":
            shainCd = this.accountReceivableCreateData.detailsForPromotionalExpensesRequestLogisticFeesList[rowIndex].shainCd;
            break;
        }
        console.log(shainCd);
        AccountReceivableCreateService.listDepartmentForItem(shainCd).then(res => {
          console.log(res);
          let op = res.data.reduce((options, item) => {
            return [...options, ...[{text: item.bumonNm, value: item}]]
          }, [])
          this.setOptionsForTable(listName, op, rowIndex, columnIndex);
        }).catch(err =>{
          console.log(err)
        });
      },
      onOpenSelectListItem(rowIndex, columnIndex, listName, fieldName) {
        this.getListItem().then(() => {
          let op = convertOptionsVueSelect.convertOptionsArray(this.listItem);
          this.controlShowSelect.manageItem = (rowIndex + 1) * 2.5 + 13;
          this.listPromotionManageItem[rowIndex][columnIndex]['options'] = op;
        });
      },
      onOpenSelectPromotionExpenses(rowIndex, columnIndex, listName, fieldName) {
        this.getListPromotionExpenses().then(() => {
          let op = convertOptionsVueSelect.convertOptionsArray(this.listPromotionExpenses);
          this.setOptionsForTable(listName, op, rowIndex, columnIndex);
        });
      },
      onOpenSelectInputType(rowIndex, columnIndex, listName, fieldName) {
        this.getListInputType().then(() => {
          let op = convertOptionsVueSelect.convertOptionsArray(this.listInputType);
          this.setOptionsForTable(listName, op, rowIndex, columnIndex);
        });
      },
      onOpenSelectClassification(rowIndex, columnIndex, listName, fieldName) {
        this.getListClassifications().then(() => {
          let op = convertOptionsVueSelect.convertOptionsArray(this.listClassification);
          this.setOptionsForTable(listName, op, rowIndex, columnIndex);
        });
      },
      onOpenSelectBrandClassification(rowIndex, columnIndex, listName, fieldName) {
        this.getListBrandClassification().then(() => {
          let op = convertOptionsVueSelect.convertOptionsArray(this.listBrandClassification);
          this.setOptionsForTable(listName, op, rowIndex, columnIndex);
        });
      },
      onOpenSelectCategory(rowIndex, columnIndex, listName, fieldName) {
        let _listNameDetailTable = ''
        switch (listName) {
          case "listPromotions8Percent":
            _listNameDetailTable = 'detailsForPromotionalExpensesRequestTax8PercentList';
            break;
          case "listPromotions10Percent":
            _listNameDetailTable = "detailsForPromotionalExpensesRequestTax10PercentList";
            break;
          case "listPromotionsCostLogistics":
            _listNameDetailTable = "detailsForPromotionalExpensesRequestLogisticFeesList";
            break;
        }
        let valueInputType = this.accountReceivableCreateData[_listNameDetailTable][rowIndex].typeOfInput;
        this.getListCategory(valueInputType).then((value) => {
          let op = convertOptionsVueSelect.convertOptionsArray(value);
          this.setOptionsForTable(listName, op, rowIndex, columnIndex);
        });
      },
      setOptionsForTable(listName, op, rowIndex, columnIndex) {
        switch (listName) {
          case "listPromotionManageItem":
            this.controlShowSelect.manageItem = (rowIndex + 1) * 2.5 + 13;
            this.listPromotionManageItem[rowIndex][columnIndex]['options'] = op;
            break;
          case "listPromotions8Percent":
            this.controlShowSelect.promotion8 = (rowIndex + 1) * 2.5 + 13;
            this.listPromotions8Percent[rowIndex][columnIndex]['options'] = op;
            break;
          case "listPromotions10Percent":
            this.controlShowSelect.promotion10 = (rowIndex + 1) * 2.5 + 13;
            this.listPromotions10Percent[rowIndex][columnIndex]['options'] = op;
            break;
          case "listPromotionsCostLogistics":
            this.controlShowSelect.logisticsCost = (rowIndex + 1) * 2.5 + 13;
            this.listPromotionsCostLogistics[rowIndex][columnIndex]['options'] = op;
            break;
        }
      },
      autoCompleteProduct: _.debounce(function (rowIndex, columnIndex, listName, fieldName, value) {
        if (value) {
          let param = {keyword: "hinmokuCd=='" + value + "%'"}
          ProductService.listProducts(param).then(res => {
            console.log(res)
            let op = res.data.content.reduce((list, item) => {
              let option = [{text: item.hinmokuCd, value: item}]
              return [...list, ...option]
            }, []);
            this.setOptionsForTable(listName, op, rowIndex, columnIndex);
          }).catch(err => {
            console.log(err);
          })
        }
      }, 1000),
      autoCompleteChargeName: _.debounce(function (rowIndex, columnIndex, listName, fieldName, value) {
        if (value) {
          this.getListChargeName(value).then(res => {
            console.log(res)
            let op = this.listChargeName.reduce((list, item) => {
              let option = [{text: item.shainNm, value: item}]
              return [...list, ...option]
            }, []);
            this.setOptionsForTable(listName, op, rowIndex, columnIndex);
          }).catch(err => {
            console.log(err);
          })
        }
      }, 1000),
      onCloseSelect() {
        this.controlShowSelect = {manageItem: 0, promotion8: 0, promotion10: 0, logisticsCost: 0}
      },
      autoCompleteStoreGroupName: _.debounce(function (rowIndex, columnIndex, listName, fieldName, value) {
        if (value) {
          AccountReceivableCreateService.listStoreGroupName(value).then(res => {
            console.log(res)
            let op = convertOptionsVueSelect.convertOptions(res.data,'storeGNm','storeGNm');
            this.setOptionsForTable(listName, op, rowIndex, columnIndex);
          }).catch(err => {
            console.log(err);
          })
        }
      }, 1000),
      //set field need to calculate
      setValueForTablePromotion(index, listName, fieldName, value, fieldIndex) {
        if (fieldName == 'bumonNm') {
          console.log(value)
          this.setDepartmentNameForRequestTable({
            bumonNm: value.bumonNm,
            index: index,
            bumonCd: value.bumonCd,
            listName: listName
          });
        }
        if (fieldName == 'shainNm') {
          console.log(value)
          this.setChargeNameForRequestTable({
            shainNm: value.shainNm,
            index: index,
            shainCd: value.shainCd,
            listName: listName
          });
        }
        //auto complete
        if (fieldName == 'hinmokuCd') {
          this.addItemAccountReceivablesProduct({
            selectedItem: {
              index: index,
              listName: listName
            }, product: value
          });
        }
        if (fieldName == 'accruedAmount') {
          this.SET_INITIAL_ACCRUE_AMOUNT({listName: listName, index: index})
        }
        console.log(index, listName, fieldName, value, fieldIndex)
        if (fieldName == 'typeOfInput') {
          switch (listName) {
            case "detailsForAccountsReceivablesRequestList":
              this.controlDisabledFieldInputType('listPromotionManageItem', index, (value == '品目コード'))
              break;
            case "detailsForPromotionalExpensesRequestTax8PercentList":
              this.controlDisabledFieldInputType('listPromotions8Percent', index, (value == '品目コード'))
              break;
            case "detailsForPromotionalExpensesRequestTax10PercentList":
              this.controlDisabledFieldInputType('listPromotions10Percent', index, (value == '品目コード'))
              break;
            case "detailsForPromotionalExpensesRequestLogisticFeesList":
              this.controlDisabledFieldInputType('listPromotionsCostLogistics', index, (value == '品目コード'))
              break;
          }

        }
      },
      controlDisabledFieldInputType(listName, index, hasEdit) {
        console.log(this[listName][index])
        this[listName][index][7]['readOnly'] = hasEdit;
        this[listName][index][8]['readOnly'] = hasEdit;
        this[listName][index][9]['readOnly'] = hasEdit;
        this[listName][index][11]['readOnly'] = !hasEdit;
        this[listName][index][13]['readOnly'] = !hasEdit;
      },
      setValueForTableManageItem(index, listName, fieldName, value, fieldIndex) {
        if (fieldName == 'bumonNm') {
          this.setDepartmentNameForRequestTable({
            bumonNm: value.bumonNm,
            index: index,
            bumonCd: value.bumonCd,
            listName: listName
          });
        }
        if (fieldName == 'shainNm') {
          console.log(value)
          this.setChargeNameForRequestTable({
            shainNm: value.shainNm,
            index: index,
            shainCd: value.shainCd,
            listName: listName
          });
        }
        //auto complete
        if (fieldName == 'hinmokuCd') {
          this.addItemAccountReceivablesProduct({
            selectedItem: {
              index: index,
              listName: listName
            }, product: value
          });
        }
        let listFieldMathDenpyoNet = /(mfYen|csdiscount|irisu|commission)/;
        if (listFieldMathDenpyoNet.test(fieldName)) {
          this.setValueDenpyoNet({index: index, listName: listName});
        }
        // set finalTakeUnitPrice
        let lisFieldFinalTakeUnitPrice = /(mfYen|accruedUnitPrice)/;
        if (lisFieldFinalTakeUnitPrice.test('finalTakeUnitPrice')) {
          this.final_take_unit_price({index: index, listName: listName});
        }
        //accrued_amount
        let ListAccruedAmount = /(accruedUnitPrice|quantityOfSoldItems)/;
        if (ListAccruedAmount.test(fieldName)) {
          this.accrued_amount({index: index, listName: listName});
        }
        //sales_amount
        let listSalesAmount = /(mfYen|accruedUnitPrice|quantityOfSoldItems|)/
        if (listSalesAmount.test(fieldName)) {
          this.sales_amount({index: index, listName: listName});
        }
        //final_marginal_profit

        let listFinalMarginalProfit = /(mfYen|accruedUnitPrice|quantityOfSoldItems|hyojyunYen|quantityOfSoldItems|)/
        if (listFinalMarginalProfit.test(fieldName)) {
          this.final_marginal_profit({index: index, listName: listName});
        }
        //finalMarginalProfitRatio
        let listMarginalProfitRatio = /(mfYen|accruedUnitPrice|quantityOfSoldItems|hyojyunYen|)/;
        if (listMarginalProfitRatio.test(fieldName)) {
          console.log(fieldName)
          this.final_marginal_profit_ratio({index: index, listName: listName});
        }
      },
      handleFormError(err){
        return Object.values(err).flat()
      },
      custom() {
        return {on: ['search:blur', 'input']};
      },
    },
    beforeRouteUpdate(to, from, next) {
      this.resetDataAccountReceivable();
      next();
    },
    beforeRouteLeave(to, from, next) {
      this.resetDataAccountReceivable();
      next();
    }
  };
</script>

